// fonts
* {
  font-family: "Banda" !important;
}
*:focus {
  background-color: #fef4dc !important;
}
// banner
.pvr__banner {
  background: linear-gradient(90deg, #517c13 0%, #8baf43 100%);
  &--content {
    width: 360px;
    margin: 0 auto;
    padding: 32px 20px 0px;
    @media (min-width: 1023px) {
      width: 1222px;
      display: flex;
      justify-content: space-between;
      //   padding-top: 51px;
      padding: 51px 0px 0px;
      align-items: flex-end;
    }
  }
  &--left {
    @media (min-width: 1023px) {
      width: 590px;
    }
  }
  .title {
    color: #ffffff;
    font-size: 36px;
    line-height: 44px;
    font-weight: 700;
    margin-bottom: 16px;
    @media (min-width: 1023px) {
      font-size: 64px;
      line-height: 78px;
      margin-bottom: 8px;
    }
  }
  .text {
    display: none;
    @media (min-width: 1023px) {
      display: block;
      color: #ffffff;
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 24px;
    }
  }
  &--right {
    display: flex;
    justify-content: center;
  }
  &--image {
    .picture {
      width: auto;
      height: auto;
    }
  }
  &--btn {
    margin-bottom: 50px;
  }
}

.btn__primary {
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;
  background-color: #fc802a;
  border-radius: 20px 0px 20px 20px;
  padding: 11px 50px;
  border: none;
  cursor: pointer;
  display: inline-block;
}

// caracteristicas
.pvr__caracteristicas {
  background: #ffffff;
  @media (min-width: 1222px) {
    background-color: #000000;
  }
  &--content {
    padding: 16px 0px;
    @media (min-width: 1222px) {
      padding: 0px;
      position: relative;
      &::after {
        position: absolute;
        content: "";
        width: 100%;
        height: 276px;
        background: linear-gradient(90deg, #517c13 0, #8baf43 100%);
        top: 0px;
        left: 0px;
        z-index: 0;
      }
    }
    .title {
      font-size: 20px;
      line-height: 25px;
      font-weight: 600;
      color: #3e6a27;
      width: 172px;
      margin-bottom: 12px;
      @media (min-width: 1222px) {
        font-size: 24px;
        line-height: 29px;
        margin-bottom: 14px;
        width: 230px;
      }
    }
    .text {
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 7px;
      width: 179px;
      @media (min-width: 1222px) {
        font-size: 16px;
        line-height: 20px;
        width: 230px;
      }
    }

    .btn__item {
      cursor: pointer;
      border: none;
      background-color: #ffffff;
      border-bottom: 0.5px solid #ababab;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding: 8px 2px;
      color: #000;
      text-decoration: none;
      &:hover {
        background: #faefe8;
      }
      &--info {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      &--number {
        display: block;
        font-size: 14px;
        line-height: 15px;
        width: 29px;
        height: 21px;
        background-color: #fc802a;
        color: #ffffff;
        margin-right: 4px;
        border-radius: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        @media (min-width: 1222px) {
          margin-right: 8px;
        }
      }
      &--text {
        font-size: 14px;
        @media (min-width: 1222px) {
          font-size: 16px;
          line-height: 24px;
        }
      }
    }
  }
  &--list {
    margin: 0 auto;
    @media (min-width: 650px) {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      width: 673px;
    }
    @media (min-width: 1222px) {
      width: 1222px;
      justify-content: space-between;
    }
  }
  &--item {
    width: 320px;
    margin: 0 auto 24px;
    border-radius: 26px;
    background: #fff;
    box-shadow: -2px -2px 0px 0px rgba(0, 0, 0, 0.04),
      2px 2px 4px 0px rgba(0, 0, 0, 0.08);
    padding: 29px 20px;
    position: relative;
    z-index: 1;
    @media (min-width: 650px) {
      margin: 0 0 24px;
    }
    @media (min-width: 1222px) {
      width: 385px;
    }
    &:first-child {
      @media (min-width: 650px) {
        margin-right: 33px;
      }
      @media (min-width: 1222px) {
        margin-right: 0px;
      }
    }
    &--image {
      position: absolute;
      top: 29px;
      right: 20px;
    }
  }
}

// preguntas frecuentes
.pvr__preguntas {
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
  @media (min-width: 650px) {
    padding-top: 40px;
    padding-bottom: 40px;
    padding-left: 30px;
    padding-right: 30px;
    background-color: #000000;
  }
  &--title {
    font-size: 24px;
    line-height: 29px;
    font-weight: 700;
    margin-bottom: 12px;
    color: #3e6a27;
    @media (min-width: 650px) {
      font-size: 36px;
      line-height: 44px;
      margin-bottom: 40px;
    }
  }
  &--content {
    width: 320px;
    margin: 0 auto;
    @media (min-width: 650px) {
      width: 673px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      background-color: #ffffff;
      border-radius: 24px;
      padding: 16px 20px;
    }
    @media (min-width: 1222px) {
      width: 1222px;
    }
  }
  &--right {
    position: relative;
  }
  &--list {
    z-index: 5;
    position: relative;
  }
  &--item {
    &--header {
      display: flex;
      align-items: center;
      cursor: pointer;
      width: 100%;
      height: 64px;
      background-color: #ffffff;
      border: none;
      margin: 0px;
      border-bottom: 1px solid #ababab;
      .title {
        color: #1f1f1f;
        font-weight: bold;
        margin-bottom: 0px;
        font-size: 14px;
        line-height: 18px;
        background-color: transparent;
        border: none;
        position: relative;
        padding: 4px 40px 4px 16px;
        text-align: left;
        width: 100%;
        height: 100%;
        @media (min-width: 650px) {
          font-size: 16px;
          line-height: 24px;
        }

        &:after {
          position: absolute;
          right: 16px;
          top: 13px;
          width: 12px;
          height: 12px;
          margin-left: 5px;
          margin-top: 8px;
          border-right: 2px solid #fc802a;
          border-bottom: 2px solid #fc802a;
          transform: rotate(45deg);
          border-radius: 1px;
          content: "";
          transition: all 0.1s;
          @media (min-width: 650px) {
            right: 16px;
          }
        }
      }

      &.c-accordion__tab--active {
        border: none;
        .title {
          &:after {
            transform: rotate(225deg);
            top: 20px;
          }
        }
        & + .c-accordion__panel {
          visibility: visible;
          visibility: visible;
          transition-duration: 250ms, 0ms;
          transition-delay: 0ms, 0ms;
        }
      }
    }
    &--body {
      height: 0;
      visibility: hidden;
      transition-delay: 0.1s;
      background-color: #faefe8;
      border-left: 2px solid #fc802a;
      @media (min-width: 900px) {
        padding-left: 16px;
      }
      .info {
        font-size: 16px;
        line-height: 22px;
        color: #565656;
        margin-bottom: 14px;
        padding: 20px;
        @media (min-width: 650px) {
          font-size: 16px;
          line-height: 22px;
          margin-bottom: 20px;
        }
      }
    }
  }
}

// esperas para vivir
.pvr__historias {
  background: #ffffff;
  @media (min-width: 1222px) {
    background-color: #000000;
  }
  &--title {
    font-size: 24px;
    line-height: 29px;
    color: #3e6a27;
    font-weight: bold;
    text-align: center;
    width: 320px;
    margin: 0 auto 20px;
    @media (min-width: 1222px) {
      font-size: 36px;
      line-height: 44px;
      margin-bottom: 60px;
      width: 100%;
    }
  }
  &--content {
    padding: 16px 0px;
    @media (min-width: 1222px) {
      padding: 24px 0px;
    }
    .title {
      font-size: 18px;
      line-height: 22px;
      font-weight: 600;
      color: #000000;
      margin-bottom: 16px;
      @media (min-width: 1222px) {
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 16px;
        width: 186px;
      }
    }
    .text {
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 0px;
      @media (min-width: 1222px) {
        font-size: 16px;
        line-height: 20px;
        width: 186px;
      }
    }
  }
  &--list {
    margin: 0 auto;
    @media (min-width: 650px) {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      width: 673px;
    }
    @media (min-width: 1222px) {
      width: 1222px;
      justify-content: space-between;
    }
  }
  &--item {
    width: 320px;
    margin: 0 auto 24px;
    border-radius: 26px;
    border-radius: 12px;
    background: #fef4dc;
    box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.08),
      -2px -2px 0px 0px rgba(0, 0, 0, 0.04);
    padding: 32px 30px;
    position: relative;
    @media (min-width: 650px) {
      margin: 0 0 24px;
    }
    @media (min-width: 1222px) {
      width: 385px;
      margin-bottom: 0px;
    }
    &:first-child {
      @media (min-width: 650px) {
        margin-right: 33px;
      }
      @media (min-width: 1222px) {
        margin-right: 0px;
      }
    }
    &--image {
      display: flex;
      justify-content: center;
      margin-bottom: 8px;
      @media (min-width: 1222px) {
        position: absolute;
        top: calc(50% - 66px);
        right: 20px;
      }
    }
  }
}
